.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.root {
  background: #fff
}

.App {
  display: flex;
  align-items: center;
  justify-content: center;
}

.MainApp {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.AppContainer {
  width: 100%;
  /* border: 1px solid #4877c6; */
  border-bottom: 1px solid #4877c6;
  display: flex;
  align-items: center;
  justify-content: center;
}

.AppView {
  min-height: 500px;
}

.title {
  color: #0A68FF;
  justify-content: center;
  display: flex;
}

.groomDetails {
  color: #0A68FF;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.rootNote {
  display: flex;
  align-items: center;
  justify-content: center;
}

.noteContainer {
  width: 900px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 10px 10px 10px;
  color: #0A68FF;
  text-align: center;
}

.footer {
  color: #0A68FF;
  margin: 5px;
  display: flex;
  justify-content: center;
  font-size: 14px;
  margin-top: auto;
  border: 1px solid #4877c6;
  text-align: center;
}